@import url(https://fonts.googleapis.com/css?family=DM+Sans:400&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Open Sans', 'Lato',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@-moz-document url-prefix() {
  .background {
    background: radial-gradient(farthest-corner at 40px 40px,
      #6076C4 0%, #C460A5 75%, #28235F 100% );
  }
}

.header {
    position: absolute;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;

    text-decoration: none;

    margin-top: 50px;
    height: 50px;
    width: 100vw;

}


.header .right {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    margin-right: 50px;
}

.nav-button {
    display: flex;
    flex-direction: row;

    align-items: right;
    justify-content: right;

    height: 3.125em;
    width: 9.375em;

    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.15em;
    text-align: right;

    color: #000000;

    background: transparent;

    margin-right: 25px;
}

.nav-button:hover {
    font-weight: 600;
}

.button {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    height: 3.125em;
    width: 9.375em;

    font-family: "lato";
    font-style: normal;
    font-weight: bold;
    font-size: 0.875em;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;

    color: #000000;

    background: transparent;
    border-radius: 15px;

    margin-right: 25px;
}

.button:hover {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: center;

    font-family: "lato";
    font-style: normal;
    font-weight: bold;
    font-size: .875em;
    line-height: 150%;
    display: flex;
    align-items: center;
    text-align: center;
    
    height: 3.125em;
    width: 9.375em;

    color: #000000;

    background: #ffffff;
    border-radius: 15px;

    margin-right: 25px;
}

.rectangle-polaroid {
    background: white;
    border-radius: 15px;

    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;

    margin-right: 15px;
    margin-bottom: 15px;

    transition: transform .3s;

    height: 12em;
}

.rectangle-polaroid:hover {
    box-shadow: 6px 6px 6px rgba(0, 0, 0, 0.025);
}

.rectangle-polaroid .imageContainer {
    margin-right: 35px;
    height: 15em;
    overflow: hidden;
}

.rectangle-polaroid .textContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-right: 35px;
    width: 100%;
    object-fit: contain;
}

.rectangle-polaroid .textContainer .big-text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 1em;
  line-height: 18px;

  margin-top: 1.5em;
  margin-bottom: 0.75em;
  margin-right: 15px;

  color: #000000;
}

.rectangle-polaroid .textContainer .small-text {
    font-family: DM Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 0.8em;
    line-height: 1.5em;
    color: #000000;
    margin-right: 35px;
    margin-bottom: 35px;
}

@media (max-width: 800px) {
    .rectangle-polaroid {
        background: white;
        border-radius: 15px;

        display: flex;
        flex-direction: column;
        align-items: flex-start;

        margin-right: 0px;

        transition: transform .3s;

        height: 100%;
        width: 20em;
    }

    .rectangle-polaroid .imageContainer {
        margin-right: 35px;
        height: 10em;
        overflow: hidden;
        border-radius: 15px 15px 0px 0px;
    }

    .rectangle-polaroid .textContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;

        width: 100%;
        object-fit: contain;
    }

    .rectangle-polaroid .textContainer .big-text {
        margin-left: 1.5em;
        margin-right: 1.5em;
    }

    .rectangle-polaroid .textContainer .small-text {
        margin-left: 1.5em;
        margin-right: 1.5em;
        margin-bottom: 1.5em;
    }

}

.landing {
    height: 100vh;
    font-family: DM Sans;

    display: flex;

    align-items: center;
    justify-content: center;

    flex-direction: row;

    background: #F6F8FB;
    background-size: cover;
}

.landing .text-container {
    width: 30vw;

    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;
}

.landing .text-container .big-text {
    font-size: 2.5em;
    font-style: normal;
    font-weight: 700;
    text-align: left;
}

.landing .text-container .text {
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: 1em;
    text-align: left;
    letter-spacing: 0.025em;
    margin-bottom: 0em;
}

.landing .text-container .link {
    font-size: 1em;
    font-style: normal;
    font-weight: 400;
    line-height: 0em;
    text-align: left;
    letter-spacing: 0.025em;
    text-decoration: none;
}


.landing .button-container {
    height: 62vh;
    width: 40vw;

    overflow: auto;

    display: flex;
    justify-content: space-between;

    flex-direction: column;
}

.image {
    height: 12em;
    width: 15em;
    object-fit: cover;
    border-radius: 15px 0px 0px 15px;
}

::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, .15);
  box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}

@media (max-width: 800px) {
    .landing {
        height: 100%;
        width: 100%;
        flex-direction: column;
    }

    .landing .text-container {
        margin-top: 10vh;
        width: 70%;
        align-items: center;
    }

    .landing .text-container .big-text {
        font-size: 2em;
        font-style: normal;
        font-weight: 700;
        text-align: center;
    }

    .landing .text-container .text {
        font-style: normal;
        font-weight: 400;
        font-size: .8em;
        line-height: 0.5em;
        text-align: left;
        letter-spacing: 0.025em;
        margin-bottom: 0em;
    }

    .landing .button-container {
        width: 25em;
        height: 100%;

        align-items: center;
    }

    .landing .button-container .image {
        height: 10em;
        width: 20em;
        object-fit: cover;
        object-position: center;
    }

    .image {
        height: 12em;
        width: 15em;
        object-fit: cover;
        border-radius: 0px 0px 0px 0px;
    }
}

.picture-button {
    max-height: 350px;
    max-width: 250px;

    box-shadow: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.05));

